import { normalize } from 'normalizr'
import * as schema from './schema'
import { getIsFetching } from '../reducers'
import * as api from '../api'

export const types = {
  FETCH_TODOS_REQUEST: 'FETCH_TODOS_REQUEST',
  FETCH_TODOS_SUCCESS: 'FETCH_TODOS_SUCCESS',
  FETCH_TODOS_FAILURE: 'FETCH_TODOS_FAILURE',
  ADD_TODO_SUCCESS: 'ADD_TODO_SUCCESS',
  TOGGLE_TODO_SUCCESS: 'TOGGLE_TODO_SUCCESS'
} 

export const fetchTodos = filter => (dispatch, getState) => {
  // Nao continua, evitando concorrencia (Race)
  if (getIsFetching(getState(), filter)) {
    // console.log('')
    return Promise.resolve()
  }

  // informa, pra evitar concorrencia
  // Pre api fetch
  dispatch({
    type: types.FETCH_TODOS_REQUEST,
    filter
  })

  // Async call
  return api.fetchTodos(filter).then(
    response =>
      dispatch({
        type: types.FETCH_TODOS_SUCCESS,
        filter,
        response: normalize(response, schema.arrayOfTodos)
      }),
    error =>
      dispatch({
        type: types.FETCH_TODOS_FAILURE,
        filter,
        message: error.message || 'Something went wrong!'
      })
  )
}
// Redux Thunk - Curry dipatch arg
export const addTodo = text => dispatch =>
  api.addTodo(text).then(response =>
    // Dentro do 'then', o fetch ja retornou
    // apos retorno portanto e solicitado o dispatch action
    dispatch({
      type: types.ADD_TODO_SUCCESS,
      response: normalize(response, schema.todo)
    })
  )

export const toggleTodo = id => dispatch =>
  api.toggleTodo(id).then(response =>
    dispatch({
      type: types.TOGGLE_TODO_SUCCESS,
      response: normalize(response, schema.todo)
    })
  )



