import { combineReducers } from 'redux'
import byId, * as fromById from './byId'
import createList, * as fromList from './createList'

/*
  Reducers Levels

  (ids, por ex, eh reducer de 4 nivel. Esta aninhado da seguinte maneira
  todoApp.listByFilter.all.ids)
  
  todoApp
  |_ byId
  |_ listByFilter
  |  |_ all
  |  |  |_ ids
  |  |  |_ isFetching,
  |  |  |_ errorMessage
  |  |_ active
  |  |  |_ (idem all)
  |  |  |_ ...
  |  |  |_ ...
  |  |_ completed
  |  |  |_ (idem all)
  |  |  |_ ... 
  |  |  |_ ...

*/

// Reducer Nivel 2
const listByFilter = combineReducers({
  all: createList('all'),
  active: createList('active'),
  completed: createList('completed')
})

// Root reducer. Nivel 1
const todoApp = combineReducers({
  /**
    Guarda todos os obj todo 
   */
  byId,
  /**
   So guarda ids. Eg:
   state.listByFilter.all = [1, 2, 3]
   state.listByFilter.active = [4, 5, 6]
 */
  listByFilter
})

export default todoApp

// Seletores, encapsulam complexidade --------
export const getVisibleTodos = (state, filter) => {
  // Recupera lista de ids pra este filter (Route) e retorna obj todos referente
  const ids = fromList.getIds(state.listByFilter[filter])
  return ids.map(id => fromById.getTodo(state.byId, id))
}

export const getIsFetching = (state, filter) => {
  return fromList.getIsFetching(state.listByFilter[filter])
}
export const getErrorMessage = (state, filter) => fromList.getErrorMessage(state.listByFilter[filter])
